.App {
  text-align: center;
}

.App-logo {
  height: 5vmin;
  pointer-events: none;
  margin-top: 50px;
}

.App-header, .App-body 
{
  background-color: #d3d3d3;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

code {
  background: #61dafb;
  border-radius: 5px;
  padding: 10px 30px;
}

form label {
  display: block;
}

form label input {
  background: transparent;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 2px solid #61dafb;
}

input[type=submit] {
  background: #61dafb !important;
}